<template>
  <img alt="Vue logo" src="./assets/logo.png" width="180">
  <h1>{{firstnameM+" "+lastnameM}}</h1>
  <button v-on:click="showlert1">เเสดงชื่อ</button>
  <button @click="showlert2">เเสดงนามสกุล</button>
  <h1>{{old}}</h1>
  <button @click="plus">เพิ่มตัวเลย</button>
  <button @click="minus">ลดตัวเลย</button>
  <button @click="plus5">เพิ่มตัวเลย + 5</button>
  <button @click="minus5">ลดตัวเลย - 5</button>
  <form action="">
    <label for="">กรุณากรอกชื่อ : </label>
    <input type="text" v-on:input="setfirstname"> 
    <label for="">กรุณากรอกนามสกุล : </label>
    <input type="text" v-on:input="setlastname">
    <label for="">กรุณากรอกชื่อเล่น : </label>
    <input type="text" v-on:input="setnickname"> 
  </form>
  <form action="">
    <label for="">กรุณากรอกอายุ : </label>
    <input type="text" v-on:input="setage"> 
    <label for="">กรุณากรอกวันเดือนปีเกิด : </label>
    <input type="text" v-on:input="setdate">
    <label for="">กรุณากรอกสาขา : </label>
    <input type="text" v-on:input="setdep">
  </form>
  <form action="">
    <label for="">กรุณากรอกห้อง : </label>
    <input type="text" v-on:input="setroom"> 
    <label for="">กรุณากรอกคติประจำใจ : </label>
    <input type="text" v-on:input="setapo">
    <label for="">กรุณากรอกสถานที่เรียนต่อ : </label>
    <input type="text" v-on:input="setcs">
  </form>
  <label for="">กรุณากรอกวิชาที่ไม่ชอบ : </label>
    <input type="text" v-on:input="setsub">

  <h3>ชื่อ : {{firstname}}</h3>
  <h3>นามสกุล : {{lastname}}</h3>
  <h3>ชื่อเล่น : {{nickname}}</h3>
  <h3>อายุ : {{age}}</h3>
  <h3>วันเดือนปี : {{date}}</h3>
  <h3>สาขา : {{dep}}</h3>
  <h3>ห้อง : {{room}}</h3>
  <h3>คติประจำใจ : {{apo}}</h3>
  <h3>สถานที่เรียนต่อ : {{cs}}</h3>
  <h3>วิชาที่ไม่ชอบ : {{sub}}</h3>
</template>

<script>

export default {
  data(){
    return{
      firstnameM : "Tiradon",
      lastnameM : "Permpoonma",
      old :19,
      firstname : "",
      lastname : "",
      nickname : "",
      age : "",
      date : "",
      dep : "",
      room : "",
      apo : "",
      cs : "",
      sub : "",
    }
  },
  methods:{
    showlert1(){
      alert(this.firstnameM)
    },
    showlert2(){
      alert(this.lastnameM)
    },
    plus(){
      this.old ++
    },
    minus(){
      this.old --
    },
    plus5(){
      this.old += 5
    },
    minus5(){
      this.old -= 5
    },
    setfirstname(event){
      this.firstname = event.target.value
    },
    setlastname(event){
      this.lastname= event.target.value
    },
    setnickname(event){
      this.nickname= event.target.value
    },
    setage(event){
      this.age= event.target.value
    },
    setdep(event){
      this.dep= event.target.value
    },
    setdate(event){
      this.date= event.target.value
    },
    setroom(event){
      this.room= event.target.value
    },
    setapo(event){
      this.apo= event.target.value
    },
    setcs(event){
      this.cs= event.target.value
    },
    setsub(event){
      this.sub= event.target.value
    }
  }
}
</script>

